html {
    box-sizing: border-box;

}

*,
::before,
::after {
    box-sizing: inherit;
}



$font-primary: "Poppins", sans-serif;
$font-secondary: "Lora", serif;
$fSize-base: 1.2rem;
$fSize-big: 1.4rem;
$fSize-bigger: 1.6rem;
$fSize-large: 2rem;
$fSize-small: 0.9rem;

$cWarning: #ff0000;
$cFont-base: #021934;
$cLight: #F8F6E3;
$cGradient: radial-gradient(circle, rgba(151, 231, 225, 1) 0%, rgba(106, 212, 221, 1) 57%, rgba(122, 162, 227, 1) 87%);
;
$spacer: 1em;
$footer-height: 100px;
$shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);




body {

    font-family: $font-primary;
    text-align: justify;
    font-size: $fSize-base ;
    color: $cFont-base;
    margin: 0;


}



h1 {
    font-family: $font-primary;
    font-size: $fSize-large;
    text-align: left;
   
}

h2 {
    font-family: $font-secondary;
    font-size: $fSize-bigger;
    text-align: left;
}

h3 {
    font-family: $font-secondary;
    font-size: $fSize-big;
    text-align: left;
}


p,
a {
    font-family: $font-secondary;
    font-weight: 500;
}

table {
    width: 100%;
    border-collapse: collapse;
    font-family: $font-secondary;
}

th,
td {
    border: 1px solid black;
    padding: 8px;
}

th {
    text-align: left;
    font-weight: bold;
}

th[colspan="2"],
td[colspan="2"] {
    background-color: lightgray;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
    font-family: $font-secondary;
    margin-bottom: 5px;
}

.content {
    &--warning {
        color: $cWarning;
    }

    &--brutal {
        color: transparent;
        cursor: pointer;
    }

}



.origin__text,
.origin__link {
    font-size: $fSize-small;
    color: $cFont-base;
}

.menu__checkbox,
.submenu__checkbox {
    position: absolute;
    left: -99999999px;
}
/*
.menu__list {
    li {
        &:first-child>.menu__link {

            background-color: #ff0000;
        }
    }
}
*/

.menu__toggler {
    cursor: pointer;
    font-weight: 700;
    font-size: $fSize-large;
    display: block;
    padding: 10px;
    background-color: rgb(252, 186, 4);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    height: 2em;
}

.menu__toggler:hover {
    background-color: gray;
}

.submenu__toggler {
    cursor: pointer;
    display: block;

}


.menu__list,
.submenu__list {
    display: none;
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: left;
    background-color: white;


}

.menu__link,
.submenu__link,
.submenu__toggler {
    display: block;
    padding: $spacer;
    text-decoration: none;
    color: $cFont-base;
    font-weight: 500;
}

.submenu__link {
    padding-left: calc(2 * $spacer);

}

.menu__link:hover,
.submenu__link:hover,
.submenu__toggler:hover {
    font-weight: 700;
    background-color: lightgray;

}


.menu__checkbox:checked~.menu__list,
.submenu__checkbox:checked~.submenu__list {
    display: block;
}

.header__logo {
    display: none;
}

.menu {
    position: absolute;
    width: 100%;

}

.content__image {
    max-width: 200px;
    display: block;
    margin: 0 auto;

}

.news {
    text-align: center;
    background-color: rgb(252, 186, 4);
    padding-top: 3em;
    h1 {text-align: center;}
}

.frame {
    max-width: 100vw;
    margin: 1em;
}

.wrapper {
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: $footer-height;
}

.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $spacer;
    font-family: $font-primary;
    font-size: $fSize-small;
    background-color: white;
    position: fixed;
    bottom: 0;
    width: 100%;
    margin-top: auto;
    height: $footer-height;

    a {
        color: $cFont-base;
        font-family: $font-primary;
        padding: 5px;

    }
}


.footer__left {
    flex: 2;
    padding: 8px;

}


.footer__right {
    flex: 1;

    padding: 8px;
    flex-shrink: 0;
    align-items: center;
}

[class*=media] {
    background-color: gray;
    text-align: center;
}

section {

    h1,
    h2,
    h3,
    p,
    a {
        padding-left: 1em;
        padding-right: 1em;
    }
}




